import { getInfo, setInfo } from '@/utils/storage'

const defaultUserInfo = { token: '', userId: '' }

export default {
  namespaced: true,
  state () {
    return {
      userInfo: getInfo() || defaultUserInfo
    }
  },
  mutations: {
    setUserInfo (state, obj) {
      // 确保obj是一个有效的对象
      state.userInfo = obj && typeof obj === 'object' ? obj : defaultUserInfo
      setInfo(state.userInfo)
    }
  },
  actions: {
    logout (context) {
      // 个人信息重置
      context.commit('setUserInfo', defaultUserInfo)
      // 购物车重置 （跨模块调用 mutations）
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {
    // 添加用户token的getter
    token (state) {
      return state.userInfo?.token || ''
    }
  }
}
