// 约定一个通用键名
const INFO_KEY = 'chenzhian_shopping_info'
const HISTORY_LIST = 'chenzhian_history_list'

// 安全的 JSON 解析函数
const safeJSONParse = (str, defaultValue) => {
  try {
    return str ? JSON.parse(str) : defaultValue
  } catch (err) {
    console.warn('JSON parse error:', err)
    return defaultValue
  }
}

// 获取个人信息
export const getInfo = () => {
  const defaultObj = { token: '', userId: '' }
  const result = localStorage.getItem(INFO_KEY)
  return safeJSONParse(result, defaultObj)
}

// 设置个人信息
export const setInfo = (obj) => {
  try {
    // 确保obj是一个有效的对象
    const validObj = obj && typeof obj === 'object' ? obj : { token: '', userId: '' }
    localStorage.setItem(INFO_KEY, JSON.stringify(validObj))
  } catch (err) {
    console.warn('Error setting user info:', err)
  }
}

// 移除个人信息
export const removeInfo = () => {
  try {
    localStorage.removeItem(INFO_KEY)
  } catch (err) {
    console.warn('Error removing user info:', err)
  }
}

// 获取搜索历史
export const getHistoryList = () => {
  const result = localStorage.getItem(HISTORY_LIST)
  return safeJSONParse(result, [])
}

// 设置搜索历史
export const setHistoryList = (arr) => {
  try {
    // 确保arr是一个数组
    const validArr = Array.isArray(arr) ? arr : []
    localStorage.setItem(HISTORY_LIST, JSON.stringify(validArr))
  } catch (err) {
    console.warn('Error setting history list:', err)
  }
}
