import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/utils/vant-ui'
import '@/styles/common.less'
// import Vant from 'vant'
// import 'vant/lib/index.css'
// // 插件初始化，把vant所有组件引入
// Vue.use(Vant)

// 清理可能损坏的本地存储数据
const cleanupStorage = () => {
  try {
    const keys = ['chenzhian_shopping_info', 'chenzhian_history_list']
    keys.forEach(key => {
      const value = localStorage.getItem(key)
      if (value) {
        try {
          JSON.parse(value)
        } catch (e) {
          console.warn(`清理损坏的本地存储数据: ${key}`)
          localStorage.removeItem(key)
        }
      }
    })
  } catch (err) {
    console.warn('清理本地存储时出错:', err)
  }
}

// 启动时清理
cleanupStorage()

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
